.footer {
  background-color: #345800;
  padding: 40px 0;
  &2 {
    background-color: #17321c;
  }
  &__inner {
    display: flex;
    align-items: flex-end;
  }
  &__right {
    flex-grow: 1;
    margin-left: 70px;
  }
  &__logo {
    display: block;
    width: 178px;
    &-mob {
      display: none;
    }
    img {
      width: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 14px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
      &:hover {
        color: #6c9561;
      }
      &:not(:first-child) {
        margin-left: 40px;
      }
    }
  }
  &__info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
  }
  &__rights {
    font-size: 14px;
    line-height: 120%;
    color: #6c9561;
    span {
      font-weight: 700;
    }
  }
  &__brevis {
    display: flex;
    align-items: center;
    margin-left: 32px;
    &-icon {
      width: 31px;
      img {
        width: 100%;
      }
    }
    &-text {
      font-size: 12px;
      line-height: 15px;
      color: #6c9561;
      margin-left: 12px;
      a {
        font-weight: 700;
        text-decoration: underline;
        text-underline-offset: 3px;
      }
    }
  }
}

@media screen and (max-width: 1450px) {
  .footer {
    &__right {
      margin-left: 57px;
    }
    &__logo {
      width: 138px;
    }
    &__info {
      margin-top: 20px;
    }
    &__list {
      li {
        font-size: 12px;
      }
    }
    &__rights {
      font-size: 12px;
    }
  }
}

@media screen and (max-width: 965px) {
  .footer {
    padding: 30px 0;
    &__right {
      margin-left: 40px;
    }
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 15px;
        }
      }
    }
  }
}

@media screen and (max-width: 740px) {
  .footer {
    &__left {
      display: none;
    }
    &__right {
      display: flex;
      margin-left: 0;
    }
    &__list {
      display: block;
      margin-left: 40px;
      li {
        &:not(:first-child) {
          margin: 16px 0 0 0;
        }
      }
    }
    &__info {
      display: block;
      order: -1;
      margin: 0;
    }
    &__rights {
      margin-top: 16px;
    }
    &__brevis {
      margin: 16px 0 0 0;
    }
    &__logo-mob {
      display: block;
      width: 138px;
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 475px) {
  .footer {
    &__inner {
      display: block;
    }
    &__left {
      display: block;
      margin-bottom: 16px;
    }
    &__right {
      flex-direction: column;
    }
    &__logo {
      &-mob {
        display: none;
      }
    }
    &__list {
      order: -1;
      margin: 0 0 16px 0;
    }
    &__rights {
      margin-top: 0;
    }
  }
}