.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
  padding: 260px 0 100px 0;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(48,82,0,0.74);
    pointer-events: none;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 1;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 250px;
    background-image: linear-gradient(0deg, rgba(255,255,255,0.9999999999999999) 0%, rgba(255,255,255,0) 100%);
  }
  &2 {
    background-image: url("../img/main-bg2.webp");
    &:after {
      background-color: rgba(23,43,34,0.74);
    }
  }
  &__inner {
    position: relative;
    z-index: 2;
  }
  &__slider {
    position: relative;
    &-nav {
      margin-top: 100px;
    }
    &-btns {
      display: flex;
      align-items: center;
    }
  }
  &__title {
    font-size: 70px;
    line-height: 120%;
    font-weight: 700;
    color: #ffffff;
  }
  &__text {
    max-width: 875px;
    color: #ffffff;
    margin-top: 35px;
  }
  &__btn {
    width: 221px;
    height: 52px;
    margin-top: 35px;
  }
  .swiper-button-prev {
    position: static;
    z-index: 2;
  }
  .swiper-button-next {
    position: static;
    z-index: 2;
    margin-left: 10px;
  }
  .swiper-pagination {
    bottom: 15px;
    &-bullet {
      border: 2px solid #ffffff;
    }
  }
}
.advantages {
  position: relative;
  z-index: 2;
  margin-top: -77px;
  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffffff;
    box-shadow: 0px 32px 51px 0px rgba(104,104,104,0.12);
    border-radius: 10px;
    padding: 50px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: calc(100% - 15px);
      height: 3px;
      background-color: #63a800;
      box-shadow: 0px 32px 51px 0px rgba(104,104,104,0.12);
      border-radius: 10px;
    }
    &2 {
      &:after {
        background: #07a31e;;
      }
    }
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-left: 30px;
    }
    &-icon {
      max-width: 57px;
      img {
        max-width: 100%;
      }
    }
    &-desc {
      max-width: 170px;
      font-size: 14px;
      line-height: 120%;
      font-weight: 600;
      color: #282728;
      margin-left: 15px;
    }
  }
}
.services {
  padding: 60px 0 30px 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 35px / 2));
    gap: 35px;
    margin-top: 35px;
  }
  &__item {
    background-image: url("../img/services-img.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 15px;
    padding: 40px 40px 30px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(-27.0deg, rgba(47,47,47,0.48) 15%, rgba(28,28,28,0.79) 84%);
      border-radius: 15px;
      transition: all 0.3s;
    }
    &:hover {
      &:after {
        background: linear-gradient(-27.0deg, rgba(99,168,0,0.84) 15%, rgba(89,151,0,0.93) 84%);
      }
      .services__item-bottom {
        opacity: 1;
        pointer-events: all;
      }
    }
    &-wrap {
      position: relative;
      z-index: 1;
    }
    &-title {
      max-width: 300px;
      font-size: 24px;
      line-height: 120%;
      font-weight: 800;
      color: #ffffff;
    }
    &-text {
      max-width: 445px;
      font-size: 15px;
      line-height: 140%;
      color: #ffffff;
      margin-top: 15px;
    }
    &-bottom {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 25px;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &-btn {
      width: 163px;
      height: 52px;
      background-color: #ffffff;
      box-shadow: 0px 14px 40px 0px #297e57;
      border-radius: 26px;
      &:hover {
        background-color: #000000;
        span {
          color: #ffffff;
        }
      }
      span {
        font-size: 16px;
        line-height: 120%;
        font-weight: 700;
        text-transform: uppercase;
      }
    }
    &-price {
      display: flex;
      align-items: center;
      font-size: 20.4px;
      line-height: 120%;
      font-weight: 400;
      color: #ffffff;
      span {
        font-size: 30px;
        line-height: 120%;
        font-weight: 800;
        margin: 0 5px;
      }
    }
    &2 {
      &:hover {
        &:after {
          background-image: linear-gradient(-27.0deg, rgba(2,163,31,0.84) 15%, rgba(1,147,27,0.93) 84%);
        }
      }
    }
  }
}
