.connection {
  padding: 30px 0;
  &__form {
    background-color: #63a800;
    border-radius: 10px;
    padding: 40px 25px;
    &2 {
      background-color: #28920b;
    }
    &-grid {
      width: calc(100% + 40px);
      display: flex;
      flex-wrap: wrap;
      margin-top: 25px;
    }
    &-group {
      width: calc(100% / 3 - 40px);
      padding: 0;
      border: none;
      position: relative;
      margin: 0 40px 22px 0;
      input {
        width: 100%;
        height: 45px;
        background-color: #ffffff;
        border-color: transparent;
        font-size: 15px;
        line-height: 120%;
        padding: 5px 12px 5px 30px;
        text-align: center;
        &::placeholder {
          color: #888888;
        }
      }
      span {
        position: absolute;
        top: 50%;
        left: 12px;
        transform: translate(0, -50%);
        width: 16px;
        img {
          width: 100%;
        }
      }
      textarea {
        width: 100%;
        height: 130px;
        background-color: #ffffff;
        border-color: transparent;
        font-size: 15px;
        line-height: 120%;
        padding: 15px 15px 15px 40px;
        resize: none;
        &::placeholder {
          color: #888888;
        }
      }
      .textarea-icon {
        top: 15px;
        transform: translate(0);
      }
    }
    .big--50 {
      width: calc(50% - 40px);
    }
    .big--100 {
      width: 100%;
    }
    &-bottom {
      display: flex;
      align-items: center;
      margin-top: 13px;
    }
    &-btn {
      flex-shrink: 0;
      width: 243px;
      height: 59px;
      background: transparent;
      border: 2px solid #ffffff;
      border-radius: 29.1px;
      font-size: 20.7px;
      line-height: 120%;
      font-weight: 700;
      text-transform: uppercase;
      color: #ffffff;
      &:hover {
        background: #000000;
      }
      img {
        width: 28px;
        flex-shrink: 0;
        margin-left: 20px;
      }
    }
    &-info {
      position: relative;
      margin-left: 28px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0;
        margin: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        opacity: 0;
        &:checked ~ label {
          &:before {
            opacity: 1;
          }
        }
      }
      label {
        font-size: 13px;
        line-height: 140%;
        color: #ccedc3;
        padding-left: 26px;
        position: relative;
        a {
          position: relative;
          z-index: 3;
          text-decoration: underline;
          text-underline-offset: 2px;
        }
        &:after {
          content: '';
          position: absolute;
          top: -1.5px;
          left: 0;
          width: 18px;
          height: 18px;
          background-color: #ffffff;
          border: 1px solid #dddddd;
          border-radius: 3px;
        }
        &:before {
          content: '';
          position: absolute;
          z-index: 1;
          top: 3px;
          left: 3.5px;
          transform: rotate(-90deg);
          width: 12px;
          height: 12px;
          background-image: url("../img/check.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: contain;
          opacity: 0;
          transition: all 0.3s;
        }
      }
    }
  }
  &__title {
    background: transparent !important;
    padding: 0 !important;
    text-align: center !important;
    display: block !important;
    color: #ffffff;
  }
}
