.content {
  position: relative;
  overflow: hidden;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-green {
  background-color: #63a800;
  box-shadow: 0px 14px 40px 0px rgba(21,135,82,0.4);
  border-radius: 26px;
  span {
    font-weight: 600;
    color: #ffffff;
  }
  &:hover {
    background-color: #569300;
  }
}
.btn-green2 {
  background-color: #07a31e;
  box-shadow: 0px 14px 40px 0px rgba(21,135,82,0.4);
  border-radius: 26px;
  span {
    font-weight: 600;
    color: #ffffff;
  }
}
.text16 {
  font-size: 16px;
  line-height: 150%;
}
.text18 {
  font-size: 18px;
  line-height: 140%;
}
.text45 {
  position: relative;
  z-index: 1;
  font-size: 45px;
  line-height: 120%;
  font-weight: 800;
  display: inline-block;
  background: #ffffff;
  padding: 0 25px;
  span {
    color: #63a800;
  }
}
.text45-2 {
  position: relative;
  z-index: 1;
  font-size: 45px;
  line-height: 120%;
  font-weight: 800;
  display: inline-block;
  background: #ffffff;
  padding: 0 25px;
  span {
    color: #07a31e;;
  }
}
.display2 {
  position: relative;
  text-align: center;
  &:after {
    content: '';
    position: absolute;
    top: 26px;
    left: 0;
    right: 0;
    width: 100%;
    height: 1px;
    background: #63a800;;
  }
}
.swiper-button-prev {
  z-index: 1;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border: 1px solid #c2c2c2;
  border-radius: 50%;
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 12px;
    height: auto;
    path {
      stroke: #8ef100;
    }
  }
  &:hover {
    svg path {
      stroke: #8ef100;
    }
  }
  &2 {
    svg {
      path {
        stroke: #02a31e;
      }
    }
    &:hover {
      svg path {
        stroke: #02a31e;
      }
    }
  }
}
.swiper-button-next {
  z-index: 1;
  width: 44px;
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
  border: 1px solid #c2c2c2;
  border-radius: 50%;
  margin-top: 0;
  &:after {
    display: none;
  }
  svg {
    width: 12px;
    height: auto;
    path {
      stroke: #8ef100;;
    }
  }
  &:hover {
    svg path {
      stroke: #8ef100;
    }
  }
}
.swiper-button-prev2 {
  svg {
    path {
      stroke: #02a31e !important;
    }
  }
  &:hover {
    svg path {
      stroke: #02a31e !important;
    }
  }
}
.swiper-button-next2 {
  svg {
    path {
      stroke: #02a31e !important;
    }
  }
  &:hover {
    svg path {
      stroke: #02a31e !important;
    }
  }
}
.swiper-button-disabled {
  opacity: 1 !important;
  svg path {
    stroke: #000000 !important;
  }
}
.swiper-pagination {
  z-index: 1;
  &-bullet {
    opacity: 1;
    width: 10px;
    height: 10px;
    background: transparent;
    border: 2px solid #282728;
    &-active {
      background: #63a800;
      border: unset !important;
    }
  }
  &2 {
    .swiper-pagination-bullet-active {
      background: #02a31e !important;
    }
  }
}
.list {
  padding: 50px 0;
  li {
    font-size: 24px;
    line-height: 120%;
    font-weight: 500;
    &:hover {
      color: #6c9561;
    }
    &:not(:first-child) {
      margin-top: 18px;
    }
  }
}

@media screen and (max-width: 1450px) {
  .text18 {
    font-size: 15px;
  }
  .text45 {
    font-size: 35px;
  }
  .text45-2 {
    font-size: 35px;
  }
  .display2 {
    &:after {
      top: 20px;
    }
  }
  .swiper-button-prev {
    width: 35px;
    height: 35px;
  }
  .swiper-button-next {
    width: 35px;
    height: 35px;
  }
}

@media screen and (max-width: 1160px) {
  .text45 {
    font-size: 30px;
  }
  .text45-2 {
    font-size: 30px;
  }
  .display2 {
    &:after {
      top: 18px;
    }
  }
}

@media screen and (max-width: 740px) {
  .text18 {
    font-size: 13px;
  }
}

@media screen and (max-width: 475px) {
  .text45 {
    font-size: 24px;
    padding: 0;
  }
  .text45-2 {
    font-size: 24px;
    padding: 0;
  }
  .display2 {
    &:after {
      display: none;
    }
  }
}