.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
}
.header-top {
  padding: 25px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__center {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 30px;
  }
  &__logo {
    width: 146px;
    display: block;
    img {
      width: 100%;
    }
  }
  &__connection {
    display: flex;
    align-items: center;
    &:hover {
      i {
        border: 1px solid #63a800;
      }
      span {
        color: #63a800;
      }
      .header-top__connection-tel {
        color: #63a800;
      }
    }
    &-group {
      display: flex;
      align-items: center;
      margin-left: 60px;
      .header-top__connection:not(:first-child) {
        margin-left: 60px;
      }
    }
    &-tel {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
      color: #ffffff;
      margin-left: 10px;
      transition: all 0.5s;
      span {
        margin-left: 0 !important;
      }
    }
    i {
      width: 33px;
      height: 33px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgba(0,156,57,0.0);
      border: 1px solid #e5e5e5;
      border-radius: 50%;
      transition: all 0.5s;
      img {
        max-width: 16px;
      }
    }
    span {
      font-size: 18px;
      line-height: 130%;
      font-weight: 500;
      color: #ffffff;
      margin-left: 10px;
      transition: all 0.5s;
    }
  }
  &__btn {
    width: 336px;
    height: 52px;
    box-shadow: unset !important;
  }
}
.header-bottom {
  border-top: 1px solid #e8e8e8;
  border-bottom: 1px solid #e8e8e8;
  padding: 15px 0;
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 16px;
      line-height: 120%;
      font-weight: 500;
      color: #ffffff;
      position: relative;
      &:not(:first-child) {
        margin-left: 55px;
      }
      &:not(:last-child) {
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -28px;
          transform: translate(0, -50%);
          width: 4px;
          height: 4px;
          background: #ffffff;
          border-radius: 50%;
        }
      }
      &:hover {
        color: #63a800;
      }
    }
    .active {
      font-weight: 700;
      color: #63a800;
    }
    &2 {
      li {
        &:hover {
          color: #02a31e;
        }
      }
      .active {
        color: #02a31e;;
      }
    }
  }
  &__mob {
    display: none;
  }
}

@media screen and (max-width: 1450px) {
  .header-top {
    &__center {
      margin: 0 20px;
    }
    &__logo {
      width: 115px;
    }
    &__connection {
      &-group {
        margin-left: 40px;
        .header-top__connection:not(:first-child) {
          margin-left: 40px;
        }
      }
      &-tel {
        font-size: 15px;
      }
      i {
        width: 27px;
        height: 27px;
        img {
          max-width: 13px;
        }
      }
      span {
        font-size: 15px;
        margin-left: 8px;
      };
    }
    &__btn {
      width: 263px;
      height: 42px;
    }
  }
  .header-bottom {
    padding: 12px 0;
    &__list {
      li {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 1160px) {
  .header-top {
    &__connection {
      &-group {
        display: block;
        margin-left: 30px;
        .header-top__connection:not(:first-child) {
          margin: 8px 0 0 0;
        }
      }
    }
  }
}

@media screen and (max-width: 980px) {
  .header-top {
    &__center {
      display: block;
    }
    &__logo {
      width: 158px;
    }
    &__connection {
      &-group {
        margin: 8px 0 0 0;
      }
      i {
        width: 24px;
        height: 24px;
        img {
          max-width: 11px;
        }
      }
    }
  }
  .header-bottom {
    &__list {
      li {
        font-size: 12px;
        &:not(:first-child) {
          margin-left: 35px;
        }
        &:not(:last-child) {
          &:after {
            right: -20px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px) {
  .header-top {
    &__center {
      display: none;
    }
    &__right {
      margin: 0 15px;
    }
    &__logo {
      width: 140px;
    }
    &__connection {
      &-tel {
        font-size: 13px;
      }
      span {
        font-size: 13px;
      }
    }
    &__btn {
      width: 210px;
      height: 40px;
    }
  }
  .header-bottom {
    &__list {
      position: fixed;
      z-index: 6;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100%;
      flex-direction: column;
      background-color: #63a800;
      padding: 150px 15px 30px;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
      li {
        &:not(:first-child) {
          margin: 18px 0 0 0;
        }
        &:not(:last-child) {
          &:after {
            display: none;
          }
        }
        &:hover {
          color: #000000;
        }
      }
      .active {
        color: #000000;
      }
    }
    &__mob {
      display: block;
    }
    &__wrap {
      display: flex;
      justify-content: center;
      .header-top__connection:not(:first-child) {
        margin-left: 50px;
      }
    }
    .header-top__btn {
      display: none;
    }
  }
  .burger {
    position: relative;
    z-index: 10;
    width: 40px;
    height: 30px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: #63a800;
      border-radius: 5px;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
    &--active {
      span {
        background: #ffffff;
        &:nth-child(1) {
          transform: rotate(-45deg);
          top: 13px;
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          transform: rotate(45deg);
          bottom: 14px;
        }
      }
    }
    &2 {
      span {
        background: #02a31e;
      }
    }
  }
}

@media screen and (max-width: 475px) {
  .header-top {
    &__right {
      display: none;
    }
    &__logo {
      width: 110px;
    }
  }
  .header-bottom {
    &__mob {
      display: flex;
      justify-content: flex-end;
      align-items: center;
    }
    .header-top__connection {
      &:not(:first-child) {
        margin-left: 15px;
      }
      span {
        display: none;
      }
      &-tel {
        display: none;
      }
    }
    .header-top__btn {
      display: flex;
      margin-left: 15px;
    }
  }
}